.banner-wrapper {
	background-image: url('http://placehold.it/1440x500?text=banner+space');
	@include calculate(500, 'min-height');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
}

.company-summary {
	background-color: white;
	@include calculate(100, 'margin-top');
	@include calculate(150, 'margin-bottom');
	.top-heading {
		color: $mediumseagreen;
		@extend .j-content-center;
	}
	p {
		@include calculate(30, "line-height");
		@include calculate(20, 'font-size');
		opacity: .7;
		text-align: center;

		i.fa-quote-left {
			transform: translateY(-10px);
		}

		i.fa-quote-right {
			transform: translateY(15px);
		}

		i {
			@include calculate(40, 'font-size');
			opacity: .4;
		}


		.first-letter {
			@include calculate(50, "font-size");
			color: $brown;
		}
	}
}